// htmlfont-size
// htmlpx101

function resetRootFZ () {
  let oHtml = document.querySelector('html');
  let w = oHtml.getBoundingClientRect().width; // px
  // html
  oHtml.style.fontSize = w / 10 + 'px';
}

resetRootFZ();

// window
window.addEventListener('resize', function () {
  resetRootFZ();
});

