import React from 'react';
import loadable from '@loadable/component';

import Loading from '@/components/loading';

const fallback = <Loading />;
// webpack
const Cnode = loadable(() => import('@/views/cnode'), { fallback });
const ThemeShow = loadable(() => import('@/views/theme-show'), { fallback });
const Home = loadable(() => import('@/views/home'), { fallback });
const WebApp = loadable(() => import('@/views/web-app'), { fallback });
const routes = [
  {
    id: 0, path: '/', component: Home, exact: true,
  },
  { id: 1, path: '/cnode', component: Cnode },
  { id: 2, path: '/themeShow', component: ThemeShow },
  { id: 3, path: '/webApp', component: WebApp },
];
export default routes;
