import React from 'react';
import {
  Route,
  Redirect,
  Switch,
  HashRouter,
} from 'react-router-dom';
import routes from './routes';

const renderRoutes = () => routes.map((ele) => (
  <Route
    exact={ele.exact}
    key={ele.id}
    path={ele.path}
    component={ele.component}
  />
));

/**
 *
 * <Route/>historylocationmatchprops
 * - props
 * - useHistoryuseLocationuseRouteMatch
 */
const Router = function () {
  return (
    <HashRouter>
      <Switch>
        {renderRoutes()}
        {/** route */}
        <Redirect from='/*' to='/' />
      </Switch>
    </HashRouter>
  );
};
export default Router;

